import ButtonBox from "components/ButtonBox/ButtonBox";
import DialogBox from "components/Dialog";
import Text from "components/Text/Text";
import { DialogProps } from "primereact/dialog";
import React from "react";
import { BUTTON_TYPE } from "utils/Constant";

interface IWarningProps extends DialogProps {
  title?: string;
  warningMessage?: string;
  onCancel?: () => void;
  onConfirm?: () => void;
  htmlMessage?: boolean;
}

const WarningDialogWithoutMemo: React.FC<IWarningProps> = (props: IWarningProps) => {
  const { visible, className, title, warningMessage, onCancel, onConfirm, onHide, htmlMessage } =
    props;

  return (
    <DialogBox
      onHide={onHide}
      visible={visible}
      modal
      closable={false}
      className={`p-6 ${className}`}
    >
      <div className="flex flex-col justify-center items-center space-y-4">
        <Text className="text-base font-semibold text-center" label={title} />
        {htmlMessage ? (
          <div className="text-xs waring-popup-html-content" dangerouslySetInnerHTML={{ __html: `<div>${warningMessage}</div>` }} />
        ) : (
          <Text className="text-xs text-center " label={warningMessage} />
        )}
        <div className="items-center">
          <div className=" flex  items-center w-full px-10">
            <div className="flex flex-col justify-start items-center mr-3">
              <div className=" ">
                <ButtonBox buttonType={BUTTON_TYPE.NO} onClick={onCancel} />
              </div>
            </div>

            <div className=" ">
              <ButtonBox buttonType={BUTTON_TYPE.YES} onClickWithLoader={onConfirm} />
            </div>
          </div>
        </div>
      </div>
    </DialogBox>
  );
};

const WarningDialog = React.memo(WarningDialogWithoutMemo);

export default WarningDialog;
